(function() {
  'use strict';

  angular
    .module('unterFrontApp', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .controller('WarehouseController',['$scope','$state', WarehouseController]);

  /** @ngInject */
  function WarehouseController($scope, $state, unterService) {
    unterService.getWarehouses().then(function(data){
      $scope.warehouses = data;
    })
  }
})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .controller('MainController',['$scope','$state', MainController]);

  /** @ngInject */
  function MainController($scope, $state) {
    $scope.user='ravago';
    $scope.pass='ravago';
    $scope.login = function(){
      if ($scope.user == $scope.username && $scope.pass == $scope.password){
        $state.go('warehouse');
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .service(unterService);

  /** @ngInject */
  function unterService($http) {
    var onSuccess = function(response){
      return response.data;
    }
    var onError = function(error){
      return error;
    }
    return {
      getWarehouses:function(){
        return $http.get("/warehouses").success(onSuccess).error(onError);
      }
    }
  }

})();
(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      }).state('warehouse',{
        url:'/warehouse',
        templateUrl: 'app/main/warehouse.html',
        controller: 'WarehouseController',
        controllerAs:'warehouse'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("unterFrontApp").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=container><div input-group><span class=input-group-addon id=basic-addon1>User</span> <input type=text ng-model=username class=form-control placeholder=Username aria-describedby=basic-addon1></div><br><div input-group><span class=input-group-addon id=basic-addon1>Password</span> <input type=password ng-model=password class=form-control placeholder=Password aria-describedby=basic-addon1></div><br><input type=button ng-click=login(); value=Login class=form-control></div>");
$templateCache.put("app/main/warehouse.html","<div><select ng-model=warehouses><option ng-repeat=\"warehouse in warehouses\" value={{warehouse.name}}>{{warehouse.names}}</option></select></div>");}]);